.Header{
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: larger;
  font-weight: bold;
  color: black;
  background-color: aquamarine;
  padding: 1.5% 1.5%;
}

.Nav{
  background-color: #333;
  padding: 10px 10px;
}
.searchForm{
  padding-left: 7%;
  display: flex;
}

#search{
  width: 11%;
  height:30px;
}

ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li{
  float: left;
  margin-left: 22%;
  display: flex;
  font-weight: bold;
  font-size: 100%;
  background-color: aliceblue;
}

li:hover{
  background-color:aquamarine;
  color: black;
}

.post{
  border-style: solid;
  border-width: 5px;
  margin: 2%;
}

.post:first-child{
  margin-top: 0;
}

.postDate{
  font-size: 0.75rem;
  margin-top:0.25rem;
}

.postBody{
  margin: 1rem 0;
}

.newPostForm{
  font-size: 35px ;
  text-align: center;
  color: #F55A00;
  margin: 0 0 .5vh 0;
  width: 50rem;
}

.newPostForm label{
  display: block;
  text-align: center;
  font-size: 20px;
  margin: 0 0 2vh 0;
}

.newPostForm input, textarea{
  display: block;
  width: 100%;
  padding: .5rem .8rem .5rem .8rem;
  margin: .9vw 46%;
  border:0;
  border-radius: 5px;
  font-size: 20px;
  background-color: cornsilk;
}

.newPostForm textarea{
  height: 10vh;
  margin-bottom: 5%;
}

.PostSubmitBtn{
  display: inline-block;
  width: 20%;
  color: red;
  cursor: pointer;
  font-size: 20px;
  margin: 0 85%;
  border-style: solid;
  border-width: 5px;
  border-color: black;
  padding:1.5%;
}

.footer{
  text-align: center;
  color: beige;
  background-color:#333;
  padding:1% 0;
  font-size: larger;
  margin-top: 18%;
}

.PostPage{
  text-align: left;
  padding: 1% 20%;
}

.PostPageContent{
  text-align: center;
}

.deletePostBtn{
  display: inline-block;
  width: 20%;
  color: red;
  cursor: pointer;
  font-size: 20px;
  margin: 2% 0;
  border-style: solid;
  border-width: 5px;
  border-color: black;
  padding:1.5%;
}